.info-section__items {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 10px;
}

.info-section__item {
    width: 150px;
    text-align: center;
    margin-bottom: 30px;
}

.info-section__item-value {
    font-size: 30px;
    margin-bottom: 10px;
    font-weight: bold;
}

.info-section__item-label {
    font-size: 12px;
}