.loader-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
}

.loader {
    border-radius: 50%;
    border: 2px solid;
    border-left-color: transparent;
    display: inline-block;
    animation: spin 1s infinite;

    height: 20px;
    width: 20px;
}

.loader--sm {
    height: 10px;
    width: 10px;
}

.loader--lg {
    height: 40px;
    width: 40px;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(359deg);
    }
}
