.faq-section {
    padding-bottom: 100px;
}

.faq-item {
    padding: 20px 0;
}

.faq-item + .faq-item {
    border-top: 1px solid var(--base-color);
}

.fag-item__header {
    display: flex;
    justify-content: space-between;
    gap: 10px;
    width: 100%;
}

.fag-item__header:hover {
    cursor: pointer;
}

.faq-item__collapser {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 35px;
    height: 35px;
    background-color: var(--base-color);
    color: #000;
    font-size: 16px;
}

.faq-item__question {
    font-weight: bold;
}

.faq-item__body {
    margin-top: 10px;
}

.faq-item__answer {
    font-size: 16px;
    line-height: 1.5;
}