.footer {
    color: #fff;
    width: 100%;
}


.footer__copyright {
    padding: 10px 0 20px;
    text-align: center;
}

.footer__socials {
    text-align: center;
    padding: 20px 0 10px;
    display: flex;
    justify-content: center;
}

.footer__honeypots {
    margin-top: 20px;
    margin-bottom: 20px;
    width: 100%;
    height: 140px;
    background-image: url('/images/honeypot.svg');
    background-position: center;
    background-repeat: repeat-x;
    background-size: 100px 120px;
}