.button {
    box-sizing: border-box;
    padding: 14px;
    border: 2px solid;
    outline: none;
    background-color: #f1f3f5;
    cursor: pointer;
    transition: opacity 0.3s ease;
    display: inline-block;
    font-size: 14px;
}
  
.button:hover {
    opacity: 0.8;
}
  
.button:active {
    opacity: 0.6;
}
  
.button + .button {
    margin-left: 1rem;
}

.button-primary {
    background-color: #000;
    color: var(--base-color);
}

.button-primary:hover {
    color: #000;
    background-color: var(--base-color);
}

.button-secondary {
    background-color: #000;
    color: var(--base-color);
}

.button--disabled {
    cursor: not-allowed;
    opacity: 0.2;
}