.connect-wallet {
    display: flex;
    align-items: center;
}

.connect-wallet__account {
    padding: 10px;
    display: inline-block;
    padding: 1rem;
    border-radius: 12px;
    outline: none;
    background-color: var(--base-color);
    border-radius: 0;
    border: 0;
    margin-right: 20px;
    font-size: 14px;
}

.connect-wallet__disconnect-button {
    background-color: transparent;
    border-color: transparent;
}

.connect-wallet__modal .modal-title {
    color: var(--base-color);
    margin-bottom: 20px;
}

.connect-wallet__modal .modal-dialog {
    position: relative;
}

.connect-wallet__modal .modal-content {
    background: #000;
    color: #fff;
    border: 10px solid var(--base-color);
    border-radius: 0;
    padding: 20px;
    z-index: 0;
}

.connect-wallet__modal .modal-dialog::after {
    content: "";
    position: absolute;
    width: 100%;
    left: 0;
    bottom: -90px;
    height: 110px;
    background-image: url('/images/honey-smudges.svg');
    background-repeat: repeat-x;
    z-index: -1;
}

.connect-wallet__option {
    display: flex;
    align-items: center;
    padding: 10px;
    border: 4px solid var(--base-color);
    gap: 10px;
}

.connect-wallet__option:hover {
    cursor: pointer;
    color: var(--base-color);
}

.connect-wallet__option + .connect-wallet__option {
    margin-top: 10px;
}

.connect-wallet__option-icon {
    width: 35px;
}

.connect-wallet__option-icon img {
    width: 100%;
    height: auto;
}