.logo {
  display: flex;
  align-items: center;
  font-size: 32px;
  font-weight: bold;
  color: #000;
  padding: 10px;
  font-family: "Jolly Lodger", cursive;
}

.logo img {
  width: 50px;
  margin-right: 10px;
}

.button {
    box-sizing: border-box;
    padding: 14px;
    border: 2px solid;
    outline: none;
    background-color: #f1f3f5;
    cursor: pointer;
    transition: opacity 0.3s ease;
    display: inline-block;
    font-size: 14px;
}
  
.button:hover {
    opacity: 0.8;
}
  
.button:active {
    opacity: 0.6;
}
  
.button + .button {
    margin-left: 1rem;
}

.button-primary {
    background-color: #000;
    color: var(--base-color);
}

.button-primary:hover {
    color: #000;
    background-color: var(--base-color);
}

.button-secondary {
    background-color: #000;
    color: var(--base-color);
}

.button--disabled {
    cursor: not-allowed;
    opacity: 0.2;
}
.loader-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
}

.loader {
    border-radius: 50%;
    border: 2px solid;
    border-left-color: transparent;
    display: inline-block;
    -webkit-animation: spin 1s infinite;
            animation: spin 1s infinite;

    height: 20px;
    width: 20px;
}

.loader--sm {
    height: 10px;
    width: 10px;
}

.loader--lg {
    height: 40px;
    width: 40px;
}

@-webkit-keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(359deg);
                transform: rotate(359deg);
    }
}

@keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(359deg);
                transform: rotate(359deg);
    }
}

.connect-wallet {
    display: flex;
    align-items: center;
}

.connect-wallet__account {
    padding: 10px;
    display: inline-block;
    padding: 1rem;
    border-radius: 12px;
    outline: none;
    background-color: var(--base-color);
    border-radius: 0;
    border: 0;
    margin-right: 20px;
    font-size: 14px;
}

.connect-wallet__disconnect-button {
    background-color: transparent;
    border-color: transparent;
}

.connect-wallet__modal .modal-title {
    color: var(--base-color);
    margin-bottom: 20px;
}

.connect-wallet__modal .modal-dialog {
    position: relative;
}

.connect-wallet__modal .modal-content {
    background: #000;
    color: #fff;
    border: 10px solid var(--base-color);
    border-radius: 0;
    padding: 20px;
    z-index: 0;
}

.connect-wallet__modal .modal-dialog::after {
    content: "";
    position: absolute;
    width: 100%;
    left: 0;
    bottom: -90px;
    height: 110px;
    background-image: url('/images/honey-smudges.svg');
    background-repeat: repeat-x;
    z-index: -1;
}

.connect-wallet__option {
    display: flex;
    align-items: center;
    padding: 10px;
    border: 4px solid var(--base-color);
    grid-gap: 10px;
    gap: 10px;
}

.connect-wallet__option:hover {
    cursor: pointer;
    color: var(--base-color);
}

.connect-wallet__option + .connect-wallet__option {
    margin-top: 10px;
}

.connect-wallet__option-icon {
    width: 35px;
}

.connect-wallet__option-icon img {
    width: 100%;
    height: auto;
}
.container {
    padding: 0 15px;
    max-width: 1280px;
    margin: 0 auto;
    width: 100%;
}
.header {
  padding: 10px 0;
  width: 100%;
  margin-bottom: 70px;
  background-color: var(--base-color);
  position: relative;
}

.header::before {
  content: "";
  position: absolute;
  width: 100%;
  bottom: -110px;
  height: 110px;
  background-image: url("/images/honey-smudges.svg");
  background-repeat: repeat-x;
  z-index: -1;
}

.header__content {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  position: relative;
  z-index: 1;
}

.header__content-buttons {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  flex-wrap: wrap;
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
}

.app-main-layout {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 30px;
  gap: 30px;
}

.text-error {
  color: #ff6b6b;
}

.hive-card {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 100%;
  height: 100%;
  color: #fff;
  background: #000;
  position: relative;
  margin-bottom: 40px;
}

.hive-card__content {
  padding: 35px;
  position: relative;
  z-index: 1;
}

.hive-card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 10px solid var(--base-color);
  z-index: 0;
}

.hive-card::after {
  content: "";
  position: absolute;
  width: 100%;
  left: 0;
  bottom: -90px;
  height: 110px;
  background-image: url("/images/honey-smudges.svg");
  background-repeat: repeat-x;
  z-index: -1;
}

.hive-card__title {
  font-size: 34px;
  margin-bottom: 30px;
  color: var(--base-color);
  font-family: "Jolly Lodger", cursive;
}

.section {
    margin: 10px 0;
    width: 100%;
}
.referral-system-section {
    margin-bottom: 40px;
}

.referral-system-section .card {
    height: auto;
}

.referral-system__link-wrapper {
    display: flex;
    grid-gap: 10px;
    gap: 10px;
    align-items: center;
    background: var(--base-color);
    color: #000;
    padding: 15px;
    margin-bottom: 10px;
}

.referral-system__link {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.referral-system__link-copy:hover {
    cursor: pointer;
}

.referral-system__copied {
    padding: 10px;
    background: rgba(2,70,29,0.1);
}
.info-item {
    display: flex;
    align-items: center;
    padding: 10px 0;
    grid-gap: 10px;
    gap: 10px;
}

.info-item--space-between {
    justify-content: space-between;
}

.info-item__label {
    font-weight: bold;
}

.info-item__value {

}

.info-item + .info-item {
    border-top: 1px solid var(--base-color);
}
.referral-levels {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 10px;
    gap: 10px;
}

.referral-level {
    flex: 1 1;
    text-align: center;
    padding: 10px;
}

.referral-level__count {
    font-size: 40px;
    font-weight: bold;
}

.referral-level__income {

}
.social-icon {
    width: 40px;
    height: 40px;
    background-color: #f1f3f5;
    border-radius: 50%;
    overflow: hidden;
    border: 2px solid var(--base-color);
    box-sizing: border-box;
}

.social-icon svg {
    width: 100%;
    height: 100%;
}

.social-icon--telegram {
    color: #29B6F6;
}

.social-icon--twitter {
    background-color: #29B6F6;
    color: #fff;
    padding: 7px;
}

.social-icon--discord {
    padding: 4px;
    color: #fff;
    background-color: #5865F2;
}
.socials {
    display: flex;
    grid-gap: 10px;
    gap: 10px;
    margin-bottom: 20px;
}

.social {

}

.social:hover {
    cursor: pointer;
}
.re-hire-section .hive-card {
    background-image: url('/images/honeypot.svg');
    background-position: bottom 20px right 20px;
    background-repeat: no-repeat;
    background-size: 100px;
}

.premium-amount {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 10px;
}

.re-hire__buttons {
    margin-bottom: 40px;
}
.info-section__items {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    grid-gap: 10px;
    gap: 10px;
}

.info-section__item {
    width: 150px;
    text-align: center;
    margin-bottom: 30px;
}

.info-section__item-value {
    font-size: 30px;
    margin-bottom: 10px;
    font-weight: bold;
}

.info-section__item-label {
    font-size: 12px;
}
.input-wrapper {
}

.input {
    padding: 10px;
    font-size: 14px;
    height: 52px;
    width: 100%;
    border: 4px solid var(--base-fade-color);
    border-radius: 0;
}

.input:focus {
    outline: none;
    border-color: var(--base-color);
}
.hire-section {}

.hire-form__inputs {
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;
    grid-gap: 20px;
    gap: 20px;
}

.hire-form__inputs .input-wrapper {
    flex: 1 1;
    min-width: 150px;
}

.hire-form__inputs .button {
    min-width: 150px;
}

.hire-form__predict-calcs {
    text-align: center;
}

.hire-form__minig-not-initialised {
    text-align: center;
    margin: 10px auto;
}
.faq-section {
    padding-bottom: 100px;
}

.faq-item {
    padding: 20px 0;
}

.faq-item + .faq-item {
    border-top: 1px solid var(--base-color);
}

.fag-item__header {
    display: flex;
    justify-content: space-between;
    grid-gap: 10px;
    gap: 10px;
    width: 100%;
}

.fag-item__header:hover {
    cursor: pointer;
}

.faq-item__collapser {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 35px;
    height: 35px;
    background-color: var(--base-color);
    color: #000;
    font-size: 16px;
}

.faq-item__question {
    font-weight: bold;
}

.faq-item__body {
    margin-top: 10px;
}

.faq-item__answer {
    font-size: 16px;
    line-height: 1.5;
}
.footer {
    color: #fff;
    width: 100%;
}


.footer__copyright {
    padding: 10px 0 20px;
    text-align: center;
}

.footer__socials {
    text-align: center;
    padding: 20px 0 10px;
    display: flex;
    justify-content: center;
}

.footer__honeypots {
    margin-top: 20px;
    margin-bottom: 20px;
    width: 100%;
    height: 140px;
    background-image: url('/images/honeypot.svg');
    background-position: center;
    background-repeat: repeat-x;
    background-size: 100px 120px;
}
*, *::before, *::after {
  box-sizing: border-box;
}

:root {
  --base-color: #F7B238;
  --base-fade-color: #BD8700;
  --text-color: #fff;
  --background-color: #000;
}

body, html {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #000;
  background: var(--background-color);
  background-image: url("/images/pattern.svg");
  background-size: 150px 150px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.text-center {
  text-align: center;
}
